























































































































































































































































































































@import 'assets/sass/index';

.form {
  box-shadow: 0 5px 15px rgba(0,0,0,.5);
  max-width: 600px;
  margin: 60px auto;
  padding: 20px;
  border-radius: 6px;
  form {
    display: flex;
    flex-direction: column;
    button {
      padding: 10px 0;
      cursor: pointer;
      text-transform: uppercase;
      background-color: #d7b5a1;
      color: #fff;
      border: 5px solid #fff8f4;
      font-size: 18px; }
    button:focus {
      outline: none; }
    hr {
      width: 100%;
      border: none;
      border-top: 1px solid rgba(0,0,0,.1);
      margin: 20px 0; }
    h2 {
      color: #414141;
      letter-spacing: .6px;
      margin: 0 0 4px; }
    label {
      padding: 5px 0;
      width: 100%;
      span {
        color: #414141;
        padding: 0 10px;
        font-size: 15px; }
      span.error {
        color: #fc7878;
        font-size: 14px; }
      input, select {
        width: calc(100% - 22px);
        border: 1px solid #ccc;
        border-radius: 3px;
        padding: 5px 10px;
        margin: 1px 0;
        transition: border .2s; }
      input:focus {
        outline: none; }
      input.invalid, select.invalid {
        border: 1px solid #fc7878; }
      select {
        width: 100%; }
      select:focus {
        outline: none; }
      select[multiple] {
        overflow: hidden; } }
    label.checkbox {
      input {
        width: auto; } } } }
